import React,{ useEffect, useState } from 'react';


export function Experience() {
  const [data, setData] = useState([]);
  const getData=()=>{
    fetch('./experience.json'
    ,{
      headers : { 
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      }
    }
    )
      .then(function(response){
        return response.json();
      })
      .then(function(myJson) {
        setData(myJson.experience)
      });
  }
  useEffect(()=>{
    getData()
  console.log('Here')

  },[])

  return(
    <div className='flex flex-col gap-12 justify-center items-center'>
      <h2 className='font-bold pb-4 md:pb-0 text-2xl'>EXPERIENCE</h2>
      {data.map((experience) => {
        return (
          <div key={experience.date} className='relative experience w-[95%] md:w-[70%] max-w-[600px] py-6 px-10 rounded-lg bg-[var(--bcg-button)]'>
            <div className='experience-line w-1 h-full absolute left-0 top-0 bg-[var(--text-accent)]'>
            {/* <div className='absolute top-[50px] left-[-8px] w-5 h-5 rounded-xl bg-blue-500'></div> */}
            </div>
            <p className="date text-xs pb-2 font-semibold">{experience.date}</p>
            <p className="text-md font-bold pb-3">{experience.role} • {experience.company}</p>
            <p className="description pb-4">{experience.description}</p>
            <ul className='flex list-none gap-3 flex-wrap text-xs'>
              {experience.skills.map((skill) => {
                return (
                  <li key={skill} className='bg-[var(--bcg-color)] px-4 py-2 rounded-3xl'>{skill}</li>
                )
              }
              ) }
            </ul>
          </div>
          )
        })}
    </div>
  )
}