import React,{ useEffect, useState } from 'react';
import YouTubeIcon from '@mui/icons-material/YouTube';
import GitHubIcon from '@mui/icons-material/GitHub';

export function Projects() {
  const [data, setData] = useState([]);
    const getData=()=>{
      fetch('./projects.json'
      ,{
        headers : { 
          'Content-Type': 'application/json',
          'Accept': 'application/json'
        }
      }
      )
        .then(function(response){
          return response.json();
        })
        .then(function(myJson) {
          setData(myJson.projects)
        });
    }
    useEffect(()=>{
      getData()
    },[])


  return (
    <div className="projects flex flex-col gap-12 mt-20">
      <h2 className='font-bold pb-4 md:pb-0 text-2xl text-center'>PROJECTS</h2>
      <div className='flex justify-evenly flex-wrap gap-8'>
      {
        data.map((project) => {
          return (
            <div key={"project" + project.name} className='project flex flex-col max-w-[95%] sm:max-w-[45%] lg:max-w-[30%] bg-[var(--bcg-button)] rounded-lg border-b-4 border-[var(--text-accent)]'>
              <div className='w-full min-h-[176px] h-[176px] overflow-hidden'>
                <img className="w-full object-cover object-left-top self-center rounded-t-lg" src={project.image} alt='project-pic'></img>
              </div>
                <div className='flex flex-col h-full justify-between pt-3 pb-6 sm:p-3'>
                <div>
                  <p className='font-bold p-3'>{project.name}</p>
                  <p className='description px-3 pb-4'>{project.description}</p>
                </div>
                <ul className='flex list-none flex-wrap gap-3 text-xs  px-3 pb-3'>
                  {project.skills.map((skill) => {
                    return (
                      <li className='bg-[var(--bcg-color)] px-4 py-2 rounded-3xl' key={"project" + project.name + skill}>{skill}</li>
                    )
                  })}
                </ul>
              </div>
              <div className='flex gap-4 px-6 pb-6 justify-end mt-auto'>
                <a href={project.url}><GitHubIcon/></a>
                {project.videourl? <a href={project.videourl}><YouTubeIcon/></a> : ''}
              </div>
            </div>
            )
          })
      }
      </div>
    </div>
  )
}