import WorkOutlineIcon from '@mui/icons-material/WorkOutline';
import FolderCopyIcon from '@mui/icons-material/FolderCopy';
import LightModeIcon from '@mui/icons-material/LightMode';
import DarkModeIcon from '@mui/icons-material/DarkMode';
import PictureAsPdfIcon from '@mui/icons-material/PictureAsPdf';
import HomeIcon from '@mui/icons-material/Home';

export default function Navigation({handleChange, isChecked, scroll}) {
  const openResume = () => {
    window.open('./resume.pdf', '_blank')
  }
  
  return (
    <div className='menu fixed flex top-14 md:top-5 gap-7 w-[260px] h-14 justify-center items-center rounded-3xl'>
      <HomeIcon onClick={() => scroll('home')}  className="menu-option cursor-pointer" />
      <WorkOutlineIcon onClick={() => scroll('experience')} className="menu-option cursor-pointer" />
      <FolderCopyIcon onClick={() => scroll('projects')}  className="menu-option cursor-pointer" />
      <PictureAsPdfIcon className="menu-option cursor-pointer" onClick={openResume} />
      {isChecked ? <LightModeIcon onClick={handleChange} className="menu-option mode cursor-pointer"/>: <DarkModeIcon onClick={handleChange} className="menu-option cursor-pointer"/> }
    </div>
  )
}