import './App.css';
import useLocalStorage from 'use-local-storage';
import { Experience } from './Components/Experience';
import { Projects } from './Components/Projects';
import WorkOutlineIcon from '@mui/icons-material/WorkOutline';
import FolderCopyIcon from '@mui/icons-material/FolderCopy';
import { blue } from '@mui/material/colors';
import LinkedInIcon from '@mui/icons-material/LinkedIn';
import GitHubIcon from '@mui/icons-material/GitHub';
import Navigation from './Components/Navigation';

function App() {
  const preference = window.matchMedia("(preferes-color-scheme: dark)").matches; // returns true/false
  const [isDark, setIsDark] = useLocalStorage('isDark', preference);
  let color
  isDark ? color = blue[700] :  color = blue[700]

  return (
    <div className='wrapper w-full' data-theme={isDark? 'dark' : 'light'}>
      <div className="relative app w-full flex flex-col justify-center items-center">
        
        <Navigation
          isChecked={isDark}
          handleChange={() => setIsDark(!isDark)}
          scroll={(id) => {
            document.getElementById(id).scrollIntoView({
              behavior: 'smooth'
            });
            }}
        />
        <div id="home" className='h-screen flex items-center'>
          <div className='flex flex-col gap-16'>
            <div className='flex justify-center mt-[100px] sm:mt-0 flex-wrap gap-20'>
              <div className='flex flex-col flex-wrap w-[176px] gap-4'>
                <img className='rounded-[100%] w-[176px] h-[176px] object-cover' src='../wsh_resized.png' alt='face'></img>
                <div className='flex gap-3 justify-center pt-4 lg:pt-0'>
                  <a href="https://github.com/annaarutyunova/">
                    <GitHubIcon sx={{fill: color }} />
                  </a>
                  <a href="https://www.linkedin.com/in/anna-arutyunova/">
                    <LinkedInIcon fontSize='medium' sx={{fill: color }} />
                  </a>
                </div>
              </div>
              <div className='flex flex-col max-w-[90%] text-center md:text-left items-center'>
                <h1 className='text-5xl inter-bold text-center'>Anna</h1>
                <div className='pt-16'>
                  <p className='text-2xl'>
                    I try to develop web and mobile apps
                  </p>
                  <p className='text-2xl'>
                    Check out my resume and projects
                  </p>
                </div>
              </div>
            </div>
            <div className='flex flex-col md:flex-row justify-center items-center gap-4 md:gap-10 '>
              <div className='option flex gap-4 items-center cursor-pointer'>
                <WorkOutlineIcon sx={{fill: color }} />
                <h2 className='accent w-fit font-bold text-xl'>Experience</h2>
              </div>
              <div className='option w-fit flex gap-4 items-center cursor-pointer'>
                <FolderCopyIcon sx={{fill: color }} />
                <h2 className='accent font-bold text-xl'>Projects</h2>
              </div>
            </div>
          </div>
        </div>
        <div className='flex flex-col gap-4'>
          <div id='experience' className='pt-20'>
            <Experience/>
          </div>
          <div id="projects">
            <Projects/>
          </div>
        </div>
      </div>
    </div>
  );
}

export default App;
